import React from 'react';

class Guide extends React.Component {
  render() {
    return (
      <div id="gtco-features">
        <div className="gtco-container">
          <div className="row">
            <div className="col-md-8 col-md-offset-2 text-center gtco-heading">
              <h2>Những lưu ý dành cho bạn</h2>
              <p>Được lập ra nhằm mục đích tổng hợp, phân loại các dự án, những cuộc thi đang diễn ra trong cộng đồng giới trẻ, chúng mình hi vọng sẽ giúp đỡ được các bạn trẻ tìm kiếm thành công những hoạt động phù hợp nhất với bản thân mình.<br /></p>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <div className="feature-left animate-box" data-animate-effect="fadeInLeft">
                <span className="icon">
                  <i className="icon-check"></i>
                </span>
                <div className="feature-copy">
                  <h3>NHẬP THÔNG TIN</h3>
                  <p>Hãy chú ý nhập đầy đủ các thông tin có trong form.</p>
                </div>
              </div>
      
              <div className="feature-left animate-box" data-animate-effect="fadeInLeft">
                <span className="icon">
                  <i className="icon-check"></i>
                </span>
                <div className="feature-copy">
                  <h3>ĐỢI DUYỆT</h3>
                  <p>Admin sẽ phê duyệt bài của bạn.<br />
                    Bạn sẽ nhận được thông báo ngay sau khi bài đăng được đặt lịch để đăng lên page.</p>
                </div>
              </div>
      
              <div className="feature-left animate-box" data-animate-effect="fadeInLeft">
                <span className="icon">
                  <i className="icon-check"></i>
                </span>
                <div className="feature-copy">
                  <h3>ĐĂNG VÀO GIỜ VÀNG</h3>
                  <p>Bài của bạn sẽ được hẹn giờ để up vào khung giờ vàng (20h - 21h).<br />
                    Vì vậy nếu chưa thấy bài của bạn chưa được đăng ngay, <b>đừng vội</b> gửi lại bài cho chúng mình nhé!</p>
                </div>
              </div>
      
            </div>
      
            <div className="col-md-12">
              <center>
                <p><br /><br />Bằng việc bấm nút &quot;Tiếp tục&quot;, bạn đồng ý với các <a href="https://sites.google.com/view/tonghopevent" target="_blank" rel="noopener noreferrer">điều khoản sử dụng</a> của fanpage</p>
                <a className="btn btn-block btn-primary" href="#/next">TIẾP TỤC ➜</a>
                <br />
                Nếu bạn gặp lỗi, hãy thông báo cho admin tại: <a href="https://m.me/tonghopevent" target="_blank" rel="noopener noreferrer">https://m.me/tonghopevent</a><br />
              </center>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Guide;