import React from 'react';

const Data = [

  // INDEX 0
  (parent) => {
    return {
      type: 'small',
      title: 'Bạn muốn đăng bài về',
      options: [
        { text: 'Dự án', subtitle: 'Hoạt động ngoại khóa', value: 'Dự án' },
        { text: 'Cuộc thi', subtitle: 'Dành cho HS, SV', value: 'Cuộc thi' },
        { text: 'Sự kiện', subtitle: 'Dành cho HS, SV' , value: 'Sự kiện' },
      ],
      notice: 'Lưu ý: Fanpage không nhận đăng bài về tuyển dụng việc làm.',
      next: (value) => {
        const newState = {
          screen: 1,
          type: value,
        };
        if (value === 'Cuộc thi') newState.tag = '#cuộc_thi';
        else if (value === 'Sự kiện') newState.tag = '#sự_kiện';
        parent.setState(newState);
      }
    };
  },

  // INDEX 1
  (parent) => {
    return {
      type: 'long',
      title: <React.Fragment>{parent.state.type} của bạn được <b>điều hành bởi</b></React.Fragment>,
      options: [
        { text: 'Một nhóm học sinh, sinh viên', subtitle: 'Hoạt động phi lợi nhuận', value: 'F1' },
        { text: 'Một câu lạc bộ trong trường học', subtitle: 'Hoạt động phi lợi nhuận', value: 'F2' },
        { text: 'Một trường học công lập', subtitle: 'VD: trường THCS, THPT, ĐH, Cao đẳng,...', value: 'F3' },
        { text: 'Một tổ chức phi lợi nhuận', subtitle: 'VD: AIESEC, VOGE, SOTB,...', value: 'F4' },
        { text: 'Tổ chức hoạt động có lợi nhuận', subtitle: 'VD: trung tâm ngoại ngữ, cửa hàng,...', value: 'P1' },
      ],
      next: (value) => {
        if (value === 'P1') {
          parent.setState({ screen: 2 });
        } else {
          window.location.replace(parent.getFormUrl(value));
        }
      }
    };
  },

  // INDEX 2
  (parent) => {
    return {
      type: 'basic',
      title: <React.Fragment>
        Dựa theo <a href="https://sites.google.com/view/tonghopevent" target="_blank" rel="noopener noreferrer">điều khoản sử dụng</a> của chúng mình, {parent.state.type.toLowerCase()} được tổ chức hoặc được điều hành bởi một hoặc nhiều doanh nghiệp, tổ chức hay cá nhân <b>hoạt động có lợi nhuận</b> phải trả phí theo <b>Gói P1</b><br/>
        <br/>
        Gói P1 có giá là 100.000 VNĐ / 1 bài đăng [<a href="https://sites.google.com/view/tonghopevent/gói-p1" target="_blank" rel="noopener noreferrer">Bấm đây để xem chi tiết</a>]<br/>
        <br/>
        Để tiếp tục, bạn hãy nhập thông tin vào form sau:
        &nbsp;
        <button className="btn btn-small btn-primary"
          style={{padding: '4px'}}
          onClick={() => {
            window.location.replace(parent.getFormUrl('P1'));
          }}
        >Tới Form ➜</button><br/>
        Sau đó, hướng dẫn thanh toán sẽ được gửi tới inbox của bạn.
      </React.Fragment>
    };
  },
];

export default Data;