import React, {useState} from 'react';
import Button from '@material-ui/core/Button';
import Utils from '../Utils';
import DatePicker from '../components/DatePicker';

const ONE_DAY_IN_MS = 86400 * 1000;
const INTERVAL_BETWEEN_POSTS_IN_MS = 15 * 60000; // 10 mins
const TIMESLOT_BEGIN_AT = (-7 + 20) * 60 * 60000; // 20h everyday GMT+7
const MIN_SCHEDULED_TIME_INTERVAL = 20 * 60000; // must schedule at least 20 mins in advance
const NUMBER_OF_DAYS = 5;
const NUMBER_OF_TIMESLOT = 9;

const generateTimeslots = () => {
  const now = Date.now();
  const beginOfDay = Math.floor(now / ONE_DAY_IN_MS) * ONE_DAY_IN_MS;

  const timeslots = [];
  let day = 0;
  while (timeslots.length < NUMBER_OF_DAYS) {
    const startAt = beginOfDay + TIMESLOT_BEGIN_AT + day*ONE_DAY_IN_MS;
    const slotsOfDay = [];
    for (let i = 0; i < NUMBER_OF_TIMESLOT; i++) {
      const time = startAt + INTERVAL_BETWEEN_POSTS_IN_MS*i;
      if (time - now > MIN_SCHEDULED_TIME_INTERVAL) {
        slotsOfDay.push(time);
      }
    }
    if (slotsOfDay.length > 2) timeslots.push({
      startAt,
      slotsOfDay,
    });
    day++;
  }

  return timeslots;
};

const isSlotTaken = (stat, time) => {
  return stat.some((t, i) => {
    if (time - INTERVAL_BETWEEN_POSTS_IN_MS/2 < t
      && t < time + INTERVAL_BETWEEN_POSTS_IN_MS/2
    ) {
      stat.splice(i, 1);
      return true;
    }
    return false;
  });
}

const ScheduledList = ({stat}) => {
  return <>
    <p><b>Các bài đăng đã lên lịch</b></p>
    <ul>
      {stat.map(s => {
        return <li key={s}>{Utils.getTimeStr(s)} ngày {Utils.getDateStr(s)}</li>
      })}
    </ul>
  </>;
}

// statArr = [int, int...]
export const TimeslotPicker = ({ statArr, value, onChange }) => {
  const [useOldPicker, setUseOldPicker] = useState(false);
  const timeslots = generateTimeslots();
  const stat = JSON.parse(JSON.stringify(statArr));
  const val = value * 1000;

  return <>
    <p>
      <b>Lên lịch bài đăng</b>{'   '}
      <Button
        variant='outlined'
        size='small'
        onClick={() => setUseOldPicker(!useOldPicker)}
      >{useOldPicker ? 'Chọn giờ theo slot' : 'Chọn giờ thủ công'}</Button>
    </p>

    {!useOldPicker && timeslots.map(day => <p key={day.startAt}>
      {Utils.getDateStr(day.startAt)}<br/>
      {day.slotsOfDay.map(time => <React.Fragment key={time}>
        <Button
          variant={val === time ? 'contained' : 'outlined'} size='small'
          onClick={() => onChange(Math.round(time/1000))}
          disabled={isSlotTaken(stat, time)}
        >{Utils.getTimeStr(time)}</Button>
        {'  '}
      </React.Fragment>)}
    </p>)}

    {useOldPicker && <>
      <DatePicker
        handleDateChange={(date) => onChange(Math.round(date))}
      />
      <ScheduledList stat={statArr} />
    </>}
  </>
}
