import React from 'react';
import Conversations from './Conversations';
import MessageView from './MessageView';
import TongHopEvent from './custom/TongHopEvent';
import { HashRouter, Route } from 'react-router-dom';
import "@chatscope/chat-ui-kit-styles/dist/default/styles.min.css";
import {
  MainContainer,
  Sidebar,
  ChatContainer,
} from "@chatscope/chat-ui-kit-react";

class Messenger extends React.Component {
  state = { tid: null };

  MessageView = () => {
    const tid = this.state.tid;
    return <div style={{ position: "relative", height: "100vh" }} className={!tid ? 'home' : 'chat'}>
      <MainContainer>
        <Sidebar position="left" scrollable={true}>
          <Conversations />
        </Sidebar>
        {tid
          ? <MessageView tid={tid} />
          : <ChatContainer />
        }
      </MainContainer>
      <TongHopEvent />
    </div>;
  }

  MessageTidChanged = ({match}) => {
    if (match.params.tid !== this.state.tid)
      this.setState({ tid: match.params.tid });
    return null;
  }

  render() {
    return (
      <HashRouter>
        <Route path="/msg" component={this.MessageView} />
        <Route path="/msg/:tid/:name" component={this.MessageTidChanged} />
      </HashRouter>
    );
  }
}

export default Messenger;