import React from 'react';
import Data from './Data';
import Utils from '../../Utils';

class Home extends React.Component {
  constructor(props) {
    super(props);
    const _psid = Utils.getPsid();
    this.psid = _psid !== 'none' ? _psid : null;
    this.state = {
      screen: 0,
      tag: null,
    };
  }

  getFormUrl(plan) {
    const psid = `${this.psid}${plan ? ';' + plan : ''}`;
    return Utils.getFormUrl(psid, this.state.tag);
  }

  componentDidMount() {
    setTimeout(window.scrollToTop, 50);
  }

  _renderSmallBtn(data) {
    return data.options.map((option, i) => {
      return (
        <button
          key={option.value + i}
          className="btn btn-default btn-proj-cate"
          onClick={() => data.next(option.value)}
        >{option.text}</button>
      );
    });
  }

  _renderLargeBtn(data) {
    return data.options.map((option, i) => {
      return (
        <button
          key={option.value + i}
          className="btn btn-default btn-proj-type"
          onClick={() => data.next(option.value)}
        >
          <b>{option.text}</b><br/>
          <small>{option.subtitle}</small>
        </button>
      );
    });
  }

  render() {
    const data = Data[this.state.screen](this);

    return (
      <div id="gtco-features">
	    	<div className="gtco-container">
	    		<div className="row">
	    			<div className="col-md-8 col-md-offset-2 text-center gtco-heading">
	    				<h2>Thông tin chung</h2>
	    				<p>
	    					{data.title}
	    				</p>
              {data.type !== 'basic' && <p>
                {data.type === 'small'
                  ? this._renderSmallBtn(data)
                  : this._renderLargeBtn(data)
                }
              </p>}
              {data.notice && <p style={{fontSize: '0.8em'}}>
                {data.notice}
              </p>}
	    			</div>
	    		</div>
	    	</div>
	    </div>
    );
  }
}

export default Home;