import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import Config from '../Config'
import Utils from '../Utils'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Img from 'react-image'
import { TimeslotPicker } from '../components/Timeslots'
import Masonry, {ResponsiveMasonry} from 'react-responsive-masonry'

const classes = {
  cardStyle: {
    marginTop: '20px',
    marginRight: '1em',
    marginLeft: '1em',
    display: 'flex',
    flexWrap: 'wrap',
  }
}

class Publish extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      completed: false,
      loading: true,
      stat: [],
      inboxData: null,
    }
    if (props.data) {
      this.state.id = props.data.id
      this.state.caption = props.data.caption
      this.state.url = props.data.url
      this.state.psid = props.data.psid
      this.state.scheduled_publish_time = 0
    }
    window.changeHeader('Công cụ đăng bài - TongHopEvent')
  }

  componentDidMount() {
    this.loadStat()
    this.loadInbox()
  }

  async loadStat() {
    const token = Utils.getLocalStorage('token')
    const res = await axios.get(`${Config.BACKEND}/publish/stat?token=${token}`)
    if (res.data.error)
      this.setState({loading: false, stat: []})
    else
      this.setState({loading: false, stat: res.data})
  }

  async loadInbox() {
    const { data } = await Utils.makeRequest(`${Config.BACKEND}/inbox/${this.state.psid}`)
    if (data && Object.keys(data).length > 0) {
      this.setState({ inboxData: data })
    }
  }
  
  async doPublish() {
    //this.setState({loading: true})
    const token = Utils.getLocalStorage('token')
    const idInDatabase = this.state.id
    const data = {
      caption: this.state.caption,
      url: this.state.url,
      scheduled_publish_time: this.state.scheduled_publish_time
    }
    const toast = window.toastr.info('Đang đăng bài...', 'Publish tool', {timeOut: 60000})
    window.location.hash = '#/'
    const res = await axios.post(`${Config.BACKEND}/publish?token=${token}`, data)
    window.toastr.clear(toast)
    if (res.data.id) {
      window.toastr.success('Đã đăng bài thành công!', 'Publish tool')
      if (window.markDocApproved) window.markDocApproved(idInDatabase)
    } else {
      window.toastr.error('Lỗi: Không thể đăng bài. Hãy báo lại cho Nui.', 'Publish tool', {timeOut: 20000})
    }
  }

  _renderComposerCard() {
    const imgStyle = {
      height: '300px',
      maxWidth: '100%',
    }
    return (
      <Card>
        <CardContent>
          <p>
            <Button
              variant='outlined'
              size='small'
              onClick={() => window.location.hash = '#/'}>Trở về</Button>
            &nbsp;&nbsp;&nbsp;
            <b>Soạn bài đăng</b>
          </p>
          <center>
            <Img alt=''
              src={this.state.url}
              style={imgStyle}
              loader={<div style={imgStyle}><center><br/><br/><CircularProgress /></center></div>}
            />  
          </center>
          <TextField
            label="Nội dung bài đăng"
            multiline
            rowsMax="20"
            value={this.state.caption}
            onChange={event => {this.setState({ caption: event.target.value });}}
            margin="normal"
            variant="outlined"
            fullWidth
          />
        </CardContent>        
      </Card>
    )
  }

  _renderPublishCard() {
    const { scheduled_publish_time, stat } = this.state
    const isDateOK = (scheduled_publish_time - (Date.now() / 1000)) > 15*60
    const s = scheduled_publish_time * 1000

    return (
      <Card>
        <CardContent>
          <TimeslotPicker
            value={scheduled_publish_time}
            statArr={stat}
            onChange={(time) => this.setState({ scheduled_publish_time: time })}
          />
          <br/><br/>
          {isDateOK && <Button variant='outlined' onClick={() => {
            window.showAlert({
              title: 'Xác nhận đăng bài',
              text: `Đăng bài vào lúc ${Utils.getTimeStr(s)} ngày ${Utils.getDateStr(s)} ?`,
              onClickOK: this.doPublish.bind(this),
              showCancel: true
            })
          }}>Đăng bài</Button>}
          {!isDateOK && scheduled_publish_time > 0
            && <p style={{color: 'red'}}>Giờ đăng bài phải cách hiện tại ít nhất 15 phút</p>
          }
        </CardContent>
      </Card>
    )
  }

  _renderCompletedCard() {
    const { scheduled_publish_time } = this.state
    const s = scheduled_publish_time * 1000

    return (
      <div style={classes.cardStyle}>
        <Card>
          <CardContent>
            <h2>Đăng bài thành công</h2>
            <p>Bài viết đã được đặt giờ để đăng vào lúc {Utils.getTimeStr(s)} ngày {Utils.getDateStr(s)}</p>
            <Button
              variant='outlined'
              onClick={() => window.location.hash = '#/'}>Trở về trang chủ</Button>
          </CardContent>
        </Card>
      <br/><br/>
      </div>
    )
  }

  _renderInboxCard() {
    const { inboxData } = this.state;
    return (
      <Card>
        <CardContent>
          {!inboxData && <center><CircularProgress /></center>}
          {inboxData && <>
            <b>Tin nhắn mới nhất (ở trên là mới, dưới là cũ)</b>
            {'   '}
            <Button
              variant='outlined'
              size='small'
              onClick={() => Utils.goToInbox(inboxData)}
            >Mở inbox</Button>
            <br/>
            ------<br/>
            {inboxData.messages.data.map(msg => <p key={msg.id}>
              <b>{msg.from.name}</b>: {msg.message}
            </p>)}
          </>}
        </CardContent>        
      </Card>
    )
  }

  _renderMain() {
    return (
      <div style={{margin: '20px'}}>
        <ResponsiveMasonry
          columnsCountBreakPoints={{350: 1, 750: 2, 900: 2}}
        >
          <Masonry gutter="10px">
            {this._renderComposerCard()}
            <div></div> {/* this is to make sure inboxCard is on the right */}
            {this._renderPublishCard()}
            {this._renderInboxCard()}
          </Masonry>
        </ResponsiveMasonry>
        <br/><br/><br/>
      </div>
    )
  }

  _renderError() {
    return (
      <center>
        <br/><br/>
        <h2>Có lỗi xảy ra</h2>
        Hãy vào trang chủ và thử lại<br/>
        <br/><br/>
      </center>
    )
  }

  render() {
    const loading = <div>
      <br/><br/><br/>
      <center><CircularProgress /></center>
    </div>

    if (this.state.loading) return loading
    if (this.state.completed) return this._renderCompletedCard()

    return this.props.data ? this._renderMain() : this._renderError()
  }
}

export default Publish