import React from 'react';
import Guide from './Guide';
import NoPsid from './NoPsid';
import OldVersion from './OldVersion';
import Utils from '../../Utils';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.psid = Utils.getPsid();
  }

  render() {
    return (
      <React.Fragment>
        <header id="gtco-header" className="gtco-cover" role="banner" style={{backgroundImage: `url(theme/images/img_bg_1.jpg`}}>
          <div className="gtco-container">
            <div className="row">
              <div className="col-md-8 col-md-offset-2 text-center">
                <div className="display-t">
                  <div className="display-tc animate-box" data-animate-effect="fadeIn">
                    <h1>Đăng dự án</h1>
                    <h2>Tổng hợp các dự án và cuộc thi dành cho học sinh, sinh viên</h2>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>

        {this.psid !== 'none'
          ? <Guide />
          : !Utils.getParameterByName('psid') ? <NoPsid /> : <OldVersion />
        }
      </React.Fragment>
    );
  }
}

export default Home;