import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import Config from '../Config'
import DialogTitle from '@material-ui/core/DialogTitle'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import PropTypes from 'prop-types'
import Utils from '../Utils'

class FBPostEditDialog extends React.Component {
  constructor(props) {
    super(props)
    console.log(props.post)
    this.state = {
      title: props.post.message.split('\n')[0] || '',
      message: props.post.message,
      loading: false,
    }
  }

  static propTypes = {
    onClose: PropTypes.func.isRequired,
    post: PropTypes.any.isRequired,
  }

  onEditPost = async () => {
    const { message } = this.state
    const { post, onClose } = this.props
    this.setState({ loading: true })
    const token = Utils.getLocalStorage('token')
    await axios.patch(`${Config.BACKEND}/publish/post/${post.id}?token=${token}`, {
      message: message
    })
    onClose(true);
  }

  render() {
    const { title, message, loading } = this.state
    const { post, onClose } = this.props

    return (
      <React.Fragment>
        <Dialog open={true} onClose={() => onClose(false)} aria-labelledby="form-dialog-title">
          {loading ? <center><br/><br/><CircularProgress /><br/><br/></center> : <>
            <DialogTitle id="form-dialog-title">Sửa bài viết</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Bài viết: {title}<br/>
                Đăng lúc: {Utils.getTimeStr(post.time)} ngày {Utils.getDateStr(post.time)}
              </DialogContentText>
              <TextField
                multiline
                rowsMax="20"
                value={message}
                onChange={event => {this.setState({ message: event.target.value })}}
                margin="normal"
                variant="outlined"
                fullWidth
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={() => onClose(false)} color="primary">
                Quay lại
              </Button>
              <Button onClick={this.onEditPost} color="primary">
                LƯU POST
              </Button>
            </DialogActions>
          </>}
        </Dialog>
      </React.Fragment>
    )
  }
}

export default FBPostEditDialog
