import React from 'react';
import {
  ConversationList,
  Conversation,
} from "@chatscope/chat-ui-kit-react";
import { removeVietnameseTones, mergeNewConversations } from './utils';
import { makeRequest } from '../../Utils';
import Socket from './socket';

class Conversations extends React.Component {
  state = {
    loading: false,
    conversations: [],
    activeConversationId: null,
    next: null,
  };

  openConversation = (id, name) => () => {
    const n = removeVietnameseTones(name.toLowerCase()).replace(/[^a-z0-9]+/g, '-');
    window.location.hash = `#/msg/${id}/${n}`;
    this.updateActiveConversation();
  }

  componentDidMount() {
    this.loadMoreConversations();
    this.updateActiveConversation();
    Socket.subscribeInbox(this.inboxUpdateHandler);
  }

  componentWillUnmount() {
    Socket.unsubscribeInbox(this.inboxUpdateHandler);
  }

  updateActiveConversation = () => {
    const matched = window.location.href.match(/#\/msg\/([a-z0-9_-]+)/);
    this.setState({ activeConversationId: matched ? matched[1] : null });
  };

  inboxUpdateHandler = async (ev) => {
    const { data: newArr } = await makeRequest('get', '/inbox/conversations', {limit: 5});
    this.setState((prevState) => ({
      conversations: mergeNewConversations(prevState.conversations, newArr),
    }));
  }

  async loadMoreConversations() {
    if (!this.state.loading) {
      this.setState({ loading: true });

      const qs = {};
      if (this.state.next) qs.after = this.state.next;
      const { data, paging } = await makeRequest('get', '/inbox/conversations', qs);

      if (data) this.setState({
        conversations: [...this.state.conversations, ...data],
        loading: false,
        next: (paging?.next) ? (paging?.cursors?.after) : null,
      });
    }
  }

  onYReachEnd = () => {
    if (this.state.next && !this.state.loading) {
      this.loadMoreConversations();
    }
  }

  render() {
    return (
      <ConversationList loadingMore={this.state.loading} onYReachEnd={this.onYReachEnd}>
        {this.state.conversations.map(conv => {
          const name = conv.senders.data[0].name;
          return (
            <Conversation
              key={conv.id}
              name={`${name} (${conv.message_count})`}
              info={conv.snippet}
              onClick={this.openConversation(conv.id, name)}
              active={this.state.activeConversationId === conv.id}
            ></Conversation>
          );
        })}
      </ConversationList>
    );
  }
}

export default Conversations;