import { io } from 'socket.io-client';
import Utils from '../../Utils';
import Config from '../../Config';

class Socket {
  static socket = null;
  static getSocket() {
    if (!Config.SOCKETIO) return null;
    if (!Socket.socket) {
      Socket.socket = io(Config.SOCKETIO, {
        transports: ['websocket'],
        query: `module=tonghopevent&token=${Utils.getToken()}`,
      });
      Socket.socket.on('connect', () => console.log('connected'));
    }
    return Socket.socket;
  }

  static subscribeInbox(handler) {
    const socket = Socket.getSocket();
    if (socket) socket.on('inbox', handler);
  }

  static unsubscribeInbox(handler) {
    const socket = Socket.getSocket();
    if (socket) socket.offAny(handler);
  }
}

export default Socket;