import Config from '../../Config';

const getMessageDirectionLastData = {arg: null, res: null};
export const getMessageDirection = (message) => {
  if (getMessageDirectionLastData.arg === message) {
    return getMessageDirectionLastData.res;
  } else {
    getMessageDirectionLastData.arg = message;
    const senderId = message.from.id;
    const isFromPage = senderId === Config.SELF_PAGE_ID || senderId === '182794865548469';
    getMessageDirectionLastData.res = isFromPage ? 'outgoing' : 'incoming';
    return getMessageDirectionLastData.res;
  }
};

export const mergeNewMessages = (oldArr, newArr) => {
  const addedMessages = [];
  const isExistInOldArr = (id) => oldArr.some(msg => msg.id === id);
  for (const msg of newArr) {
    if (isExistInOldArr(msg.id)) break;
    addedMessages.unshift(msg);
  }
  return [...oldArr, ...addedMessages];
};

export const mergeNewConversations = (oldArr, newArr) => {
  const convIds = {};
  return [...newArr, ...oldArr].filter(conv => {
    if (!convIds[conv.id]) {
      convIds[conv.id] = true;
      return true;
    } else {
      return false;
    }
  });
}

export const getTargetId = (ev) => {
  if (ev.recipient && ev.recipient.id !== Config.SELF_PAGE_ID) {
    return ev.recipient.id;
  } else if (ev.sender && ev.sender.id !== Config.SELF_PAGE_ID) {
    return ev.sender.id;
  } else {
    return null;
  }
}

/*eslint-disable */
export const removeVietnameseTones = (str) => {
  str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g,"a"); 
  str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g,"e"); 
  str = str.replace(/ì|í|ị|ỉ|ĩ/g,"i"); 
  str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g,"o"); 
  str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g,"u"); 
  str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g,"y"); 
  str = str.replace(/đ/g,"d");
  str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
  str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
  str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
  str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
  str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
  str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
  str = str.replace(/Đ/g, "D");
  // Some system encode vietnamese combining accent as individual utf-8 characters
  // Một vài bộ encode coi các dấu mũ, dấu chữ như một kí tự riêng biệt nên thêm hai dòng này
  str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, ""); // ̀ ́ ̃ ̉ ̣  huyền, sắc, ngã, hỏi, nặng
  str = str.replace(/\u02C6|\u0306|\u031B/g, ""); // ˆ ̆ ̛  Â, Ê, Ă, Ơ, Ư
  // Remove extra spaces
  // Bỏ các khoảng trắng liền nhau
  str = str.replace(/ + /g," ");
  str = str.trim();
  // Remove punctuations
  // Bỏ dấu câu, kí tự đặc biệt
  str = str.replace(/!|@|%|\^|\*|\(|\)|\+|\=|\<|\>|\?|\/|,|\.|\:|\;|\'|\"|\&|\#|\[|\]|~|\$|_|`|-|{|}|\||\\/g," ");
  return str;
};
/*eslint-enable */