import React from 'react';
import nl2br from 'react-nl2br';
import Loader from '@chatscope/chat-ui-kit-react/dist/cjs/Loader';
import { makeRequest } from '../../../Utils';

function Card({children}) {
  return <div style={{
    padding: '10px',
    marginBottom: '10px',
    border: '#cecece solid 2px',
    position: 'relative',
  }}>{children}</div>
}

class TongHopEvent extends React.Component {
  state = {
    templates: [],
    loading: true,
    editingIndex: null,
    editingText: null,
    showing: false,
  };

  componentDidMount() {
    this.loadTemplates();
    window.openTemplateDialog = this.openTemplateDialog;
  }

  openTemplateDialog = () => {
    this.setState({ showing: true });
  };

  loadTemplates = async () => {
    this.setState({ loading: true });
    let templates = (await makeRequest('get', '/inbox/templates'));
    templates = Object.keys(templates).map(id => ({ id, ...templates[id] }));
    this.setState({
      templates, loading: false,
      editingIndex: null, editingText: null
    });
  }

  save = async () => {
    const { templates, editingIndex, editingText } = this.state;
    this.setState({ loading: true });
    await makeRequest('post', '/inbox/templates', {}, {
      id: templates[editingIndex].id,
      text: editingText ? editingText : '(empty)',
    });
    this.loadTemplates();
  }

  fillToInput = (t) => () => {
    window.setMessageInput(t.text);
    this.setState({ showing: false });
  }

  render() {
    const { loading, templates, editingIndex, editingText, showing } = this.state;
    if (!showing) return null;

    return <div style={{
      position: 'fixed',
      maxWidth: '100vw',
      width: '600px',
      height: '80vh',
      Maxheight: '80vh',
      background: 'white',
      zIndex: '100',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      border: '#bbb solid 2px',
      padding: '10px',
      overflow: 'auto',
    }}>
      <button style={{ position: 'absolute', right: '10px' }} onClick={() => this.setState({ showing: false })}>[x] Thoát</button>
      <br/><br/>
      {!loading && templates.map((t, i) => {
        return editingIndex === i
          ? <Card key={i}>
            <textarea
              style={{width: '100%'}}
              value={editingText}
              onChange={(e) => this.setState({ editingText: e.target.value })}
              rows={5}
            ></textarea>
            <button onClick={this.save}>Save</button>
            {'  '}
            <button onClick={() => this.setState({ editingIndex: null, editingText: null })}>Cancel</button>
          </Card>
          : <Card key={i}>
            <p style={{cursor: 'pointer'}} onClick={this.fillToInput(t)}>
              {nl2br(t.text)}
            </p>
            <button onClick={() => this.setState({ editingIndex: i, editingText: t.text })}>Edit</button>
            {'  '}
            <button onClick={this.fillToInput(t)}>COPY &amp; PASTE</button>
            {'  '}
            {/* <button onClick={() => }>Delete</button> */}
          </Card>
      })}
      {loading && <center><Loader /></center>}
    </div>
  }
}

export default TongHopEvent;