import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import Config from '../Config'
import Utils from '../Utils'
import Doc from '../components/Doc'
import ToolsDialog from '../components/ToolsDialog'
import DuplicateDocDialog from '../components/DuplicateDocDialog'
import DuplicateHelper from '../DuplicateHelper'

class Home extends React.Component {
  constructor(props) {
    super()
    this.props = props
    this.state = {
      loading: true,
      list: [],
      toolsDialogOpen: false,
      toolsDialogDoc: {},
      duplicateHelper: new DuplicateHelper(),
      duplicateDocDialog: null,
      selectedDocID: '',
    }
    window.changeHeader('Quản lý dữ liệu - TongHopEvent')
  }

  componentDidMount() {
    this.loadData()
    window.markDocApproved = this.markDocApproved
  }

  componentWillUnmount() {
    window.markDocApproved = null
  }

  markDocApproved = (id) => {
    if (!window.DOCID_APPROVED) window.DOCID_APPROVED = {}
    window.DOCID_APPROVED[id] = true
    this.dispatchDocApproved()
  }

  dispatchDocApproved = () => {
    if (!window.DOCID_APPROVED) window.DOCID_APPROVED = {}
    this.setState(prevState => ({
      list: prevState.list.map(doc => {
        if (window.DOCID_APPROVED[doc.id]) doc.approved = true
        return doc
      })
    }))
  }

  async loadData() {
    const duplicateHelper = new DuplicateHelper()
    const token = Utils.getToken()
    const [res, {data: lookbackArr}] = await Promise.all([
      axios.get(`${Config.BACKEND}/feed?token=${token}`),
      axios.get(`${Config.BACKEND}/lookback?token=${token}`),
    ])
    Utils.checkError(res)
    duplicateHelper.pushLookbackData(lookbackArr);
    this.setState({
      loading: false,
      list: res.data,
      duplicateHelper,
    })
    this.dispatchDocApproved()
  }

  handleChange = name => event => {
    this.setState({ [name]: event.target.value })
  }

  onClickEdit() {
    this.setState({edit: true})
  }

  setDoc(i, data) {
    this.setState(state => {
      const list = state.list
      list[i] = data
      return {list}
    })
  }

  openToolsDialog(doc) {
    this.setState({
      toolsDialogOpen: true,
      toolsDialogDoc: doc
    })
  }

  openDuplicateDocDialog(docs, docid) {
    this.setState({
      duplicateDocDialog: docs,
      selectedDocID: docid,
    })
  }

  render() {
    var classes = {
      input: {
        marginBottom: '20px',
        width: '500px',
        maxWidth: '100%'
      },
      container: {
        display: 'flex',
        flexWrap: 'wrap',
      },
      cardStyle: {
        //maxWidth: '800px',
        marginTop: '20px',
        marginRight: '1em',
        marginLeft: '1em',
        display: 'flex',
        flexWrap: 'wrap',
      }
    }

    const loading = <div>
      <br/><br/><br/>
      <center><CircularProgress /></center>
    </div>

    const { gotoPublish } = this.props
    const { duplicateDocDialog, duplicateHelper, selectedDocID } = this.state
    const home = (
      <React.Fragment>
        <div style={classes.cardStyle} className="bootstrap-wrapper no-gutters">
          {
            this.state.list.length === 0 && <center><h2>Chưa có bài đăng nào</h2></center>
          }
          {this.state.list.map((doc, i) => {
            return doc ? (
              <div key={doc.id} className="col col-xl-4 col-lg-6 col-sm-12">
                <Doc
                  doc={doc}
                  setDoc={this.setDoc.bind(this)}
                  i={i}
                  admin
                  gotoPublish={gotoPublish}
                  openToolsDialog={this.openToolsDialog.bind(this)}
                  openDuplicateDocDialog={this.openDuplicateDocDialog.bind(this)}
                  duplicateHelper={duplicateHelper}
                />
              </div>
            ) : null
          })}
        </div>
        <ToolsDialog
          open={this.state.toolsDialogOpen}
          closeToolsDialog={() => this.setState({toolsDialogOpen: false})}
          name={this.state.toolsDialogDoc.name}
          psid={this.state.toolsDialogDoc.psid}
          doc={this.state.toolsDialogDoc}
        />
        <DuplicateDocDialog
          docs={duplicateDocDialog}
          selectedDocID={selectedDocID}
          closeDuplicateDocDialog={() => this.setState({duplicateDocDialog: null})}
        />
      </React.Fragment>
    )

    return this.state.loading ? loading : home
  }
}

export default Home