import React from 'react';
import {
  ConversationHeader,
  Avatar,
  InfoButton,
  MessageList,
  ChatContainer,
  Message,
  MessageInput,
} from "@chatscope/chat-ui-kit-react";
import { makeRequest } from '../../Utils';
import { getMessageDirection, getTargetId, mergeNewMessages } from './utils';
import TimeAgo from 'javascript-time-ago';
import en from 'javascript-time-ago/locale/en';
import Socket from './socket';
import Config from '../../Config';
import './styles.css';

TimeAgo.addLocale(en);
const timeAgo = new TimeAgo('en-US');

class MessageView extends React.Component {
  state = {
    loading: true,
    loadingMore: false,
    psid: null,
    profile: {},
    threadInfo: {},
    messages: [],
    messageAfter: null,
    messagePending: null,
    messagePendingId: null,
    input: '',
  }
  originalTitle = document.title;

  componentDidMount() {
    this.fetchConversation(this.props.tid);
    Socket.subscribeInbox(this.inboxUpdateHandler);
    window.setMessageInput = (input) => this.setState({ input });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.tid !== this.props.tid) {
      this.fetchConversation(this.props.tid);
    }
  }

  componentWillUnmount() {
    document.title = this.originalTitle;
    Socket.unsubscribeInbox(this.inboxUpdateHandler);
  }

  inboxUpdateHandler = async (ev) => {
    const { psid, messagePendingId } = this.state;
    const tid = this.props.tid;
    if (getTargetId(ev) === psid) {
      const { data: newMessages } = await makeRequest('get', '/inbox/thread/messages', {tid, limit: 5});
      this.setState((prevState) => ({
        messages: mergeNewMessages(prevState.messages, newMessages),
      }));
    }
    if (ev.message?.mid === messagePendingId) {
      this.setState({ messagePending: null, messagePendingId: null });
    }
  }

  async fetchConversation(tid) {
    document.title = Config.INBOX_TITLE;
    this.setState({ loading: true });
    const [threadInfo, messages] = await Promise.all([
      makeRequest('get', '/inbox/thread/info', {tid}),
      makeRequest('get', '/inbox/thread/messages', {tid}),
    ]);
    messages.data = messages.data.reverse();
    const psid = threadInfo.senders.data[0].id;
    const profile = await makeRequest('get', '/inbox/profile', { psid });
    this.setState({
      threadInfo,
      psid,
      profile,
      messages: messages.data,
      messageAfter: (messages.paging?.next) ? (messages.paging?.cursors?.after) : null,
      loading: false,
    });
    document.title = `${profile.name} - ${Config.INBOX_TITLE}`;
  }

  onYReachStart = async () => {
    const {messageAfter, loadingMore} = this.state;
    if (messageAfter && !loadingMore) {
      this.setState({ loadingMore: true });
      const messages = await makeRequest('get', '/inbox/thread/messages', {
        tid: this.props.tid,
        after: messageAfter,
      });
      this.setState((prevState) => {
        const newMsgArr = messages.data.reverse().concat(prevState.messages);
        return {
          messages: newMsgArr,
          messageAfter: (messages.paging?.next) ? (messages.paging?.cursors?.after) : null,
          loadingMore: false,
        };
      });
    }
  }

  openConversationOnFB = () => {
    window.open(`https://fb.com${this.state.threadInfo.link}`, '_blank');
  }

  openInNewTab = (url) => () => {
    window.open(url, '_blank');
  }

  openMainMenu = () => {
    window.location.hash = '#/';
  }

  _renderHeader() {
    const {loading, profile} = this.state;
    return loading ? (
      <ConversationHeader>
        <ConversationHeader.Back onClick={this.openMainMenu} />
        <ConversationHeader.Content userName="Loading..." />        
      </ConversationHeader>
    ) : (
      <ConversationHeader>
        <ConversationHeader.Back onClick={this.openMainMenu} />
        <Avatar
          src={profile.profile_pic}
          name={profile.name}
          onClick={this.openConversationOnFB}
        />
        <ConversationHeader.Content userName={profile.name} />
        <ConversationHeader.Actions>
          <InfoButton onClick={this.openConversationOnFB} />
        </ConversationHeader.Actions>          
      </ConversationHeader>
    );
  }

  _renderMessages() {
    const {loading, messages, messagePending, loadingMore} = this.state;
    if (loading) return <MessageList loading/>

    let lastDirection = '';
    const showTimeHeader = (msg) => {
      const direction = getMessageDirection(msg);
      if (lastDirection !== direction) {
        lastDirection = direction;
        return <Message.Footer sender={timeAgo.format(new Date(msg.created_time))} />
      } else {
        return null;
      }
    };

    const showTextMsg = (msg) => <Message key={msg.id} model={{
      message: msg.message,
      sentTime: timeAgo.format(new Date(msg.created_time)),
      sender: msg.from.name,
      direction: getMessageDirection(msg),
      position: 'single',
    }}>
      {showTimeHeader(msg)}
    </Message>

    const getImages = (msg) => (msg.attachments?.data || [])
      .filter(a => !!a.image_data)
      .map(a => a.image_data);

    const showImages = (msg, images) => images.map((img, i) => (
      <Message key={msg.id + i} type="image" model={{
        direction: getMessageDirection(msg),
        type: 'custom',
      }}>
        {showTimeHeader(msg)}
        <Message.CustomContent>
          <img src={img.preview_url} alt="" onClick={this.openInNewTab(img.url)} style={{width: '200px'}}></img>
        </Message.CustomContent>
      </Message>
    ));

    const showPendingMsg = () => <Message model={{
      message: messagePending,
      sender: Config.INBOX_TITLE,
      direction: 'outgoing',
      position: 'single',
    }} className="pending" />

    return (
      <MessageList loadingMore={loadingMore} onYReachStart={this.onYReachStart}>
        {messages.map(msg => {
          const images = getImages(msg);
          if (images.length > 0) {
            return <>
              {showImages(msg, images)}
            </>
          } else {
            return showTextMsg(msg);
          }
        })}
        {messagePending && showPendingMsg()}
        {!Config.INBOX_SEND_ABILITY && // add margin bottom
          <Message style={{ marginBottom: '2em', opacity: '0' }} />
        }
      </MessageList>
    );
  }

  handleInputChange = (_, v, innerText) => this.setState({ input: innerText });
  handleSendMessage = async () => {
    const messagePending = this.state.input.trim()
    this.setState({ messagePending, input: '' });
    try {
      const { message_id } = await makeRequest('post', '/inbox/messages', {}, {
        messaging_type: 'MESSAGE_TAG',
        tag: 'HUMAN_AGENT',
        recipient: { id: this.state.psid },
        message: { text: messagePending },
      });
      this.setState({ messagePendingId: message_id });
    } catch (e) {
      this.setState({ messagePending: null, input: messagePending });
      window.alert(JSON.stringify(e.response.data));
    }
  };
  _renderInput() {
    const { input, messagePending } = this.state;
    return <MessageInput
      value={input.replace(/\n/g, '<br>')}
      onChange={this.handleInputChange}
      onSend={this.handleSendMessage}
      disabled={!!messagePending}
      sendDisabled={!!messagePending && input.length > 0}
      sendOnReturnDisabled={true}
      onAttachClick={window.openTemplateDialog}
      placeholder="Type here..."
    />
  }

  render() {
    return (
      <ChatContainer>
        {this._renderHeader()}
        {this._renderMessages()}
        {Config.INBOX_SEND_ABILITY && this._renderInput()}
      </ChatContainer>
    );
  }
}

export default MessageView;